.header-image {
  background: url('blueberries.jpg');
  background-size: cover;
  width: 100%;
  display: block;
  height: 200px;
  -webkit-box-shadow: 0 1px 5px grey;
          box-shadow: 0 1px 5px grey;
}

.header-wrapper {
  height: 200px;
  position: relative;
}

.site-title {
  z-index: 0;
  text-align: center;
  font-size: 60px;
  color: white;
  margin-top: 0;
  position: absolute;
  top: 50%;
  -webkit-transform: translateY(-50%);
      -ms-transform: translateY(-50%);
          transform: translateY(-50%);
  width: 100%;
  text-align: center;
}


